body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__marker {
  transform-origin: 50% 100%;
  border-radius: .75vmin;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.stopwatch__marker--minutes {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-minutes;
}

.stopwatch__marker--seconds {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-seconds;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotate-minutes {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}

@keyframes rotate-seconds {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}

.stopwatch--speed-up .stopwatch__marker--minutes {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__marker--seconds {
  animation-duration: 10s;
}
